'use strict';

var React = require('react-native');
var {
  StyleSheet,
  View,
} = React;
var UIExplorerListBase = require('./UIExplorerListBase');

var COMPONENTS = [
  require('./ActivityIndicatorExample'), 
  // require('./DatePickerIOSExample'),
  require('./DrawerLayoutExample.web'),
 //require('./ImageExample'),//暂不支持 nativeImageSource
  require('./ScrollViewExample'),
  require('./SegmentedControlIOSExample'),
  require('./SwitchExample'),
 // require('./Navigator/NavigatorExample'),//有些问题nativeImageSource
  require('./TextExample.ios'),
 // require('./TextInputExample.ios'), //好像不支持？
 // require('./TabBarIOSExample'), //有些问题
  require('./TouchableExample'),
  require('./ListViewExample'),
  require('./ListViewGridLayoutExample'),
 // require('./ListViewPagingExample'), //不支持
  require('./PickerIOSExample'), 
    require('./PickerExample'),
  require('./ModalExample'),
  require('./ViewExample'),
    // require('./WebViewExample'),
   
 //  require('./DGTExample'),
  require('./ViewPagerAndroidExample.android'),
  require('./SliderExample'), 
  require('./ProgressViewIOSExample'),
  //require('./ButtonExample'),
];

var APIS = [
  // require('./AccessibilityAndroidExample.android'),
  require('./AlertIOSExample'),
  require('./AnimatedExample'),
 // require('./AsyncStorageExample'),//暂不支持
  require('./BorderExample'),
 // require('./LayoutEventsExample'),//暂不支持
  require('./LayoutExample'),
  require('./PanResponderExample'),
  //require('./AlertExample'),zanbu
//  require('./PointerEventsExample'),//不支持
  require('./TimerExample'),
  require('./ToastAndroidExample.android'), //部分不支持,支持前两个
  //require('./BoxShadowExample')//后加的，不支持
  // require('./XHRExample'),
  //require('./SwipeableListViewExample')
];

type Props = {
  onSelectExample: Function,
  isInDrawer: bool,
};

class UIExplorerList extends React.Component {
  props: Props;

  render() {
    return (
      <UIExplorerListBase
        components={COMPONENTS}
        apis={APIS}
        searchText=""
        renderAdditionalView={this.renderAdditionalView.bind(this)}
        onPressRow={this.onPressRow.bind(this)}
      />
    );
  }

  renderAdditionalView(renderRow, renderTextInput): React.Component {
    if (this.props.isInDrawer) {
      var homePage = renderRow({
        title: 'UIExplorer',
        description: 'List of examples',
      }, -1);
      return (
        <View>
          {homePage}
        </View>
      );
    }
    return renderTextInput(styles.searchTextInput);
  }

  onPressRow(example: any) {
    var Component = UIExplorerListBase.makeRenderable(example);

    this.props.navigator.push({
      name: 'example',
      example: Component,
      title : example.title
    });

  }
}

var styles = StyleSheet.create({
  searchTextInput: {
    padding: 2,
  },
});

module.exports = UIExplorerList;



